.sidebar-header {
    font-family: "Titillium Web",Helvetica,Arial,sans-serif;
    font-size: 22px;
    font-weight: 600;
    margin-top: 16px !important;
    margin-bottom: 0;
    color: #0a304e;
    line-height: 1.1;
    cursor: pointer;
}

.sidebar-header:hover {
    color: #0a304e; 
}

.page-sidebar-menu {
    list-style: none;
    margin: 10px 0 0;
    padding: 0;
}

.nav-link {
    text-shadow: none;
    text-decoration: none;
    margin-left: 10px;
    color: #626262;
    font-size: 18px;
    font-weight: 400;
    position: relative;
    border: 0;
    padding: 5px 0
}

.nav-link:hover {
    text-decoration: underline;
    color: #626262;
}
.page-sidebar-menu li a:hover {
    text-decoration: underline;
}
.page-sidebar-menu .nav-link.active {
    background-color: #f5f5f5;
    border-left: 2px solid #ddd;
    padding-left: 10px;
}
.nav-link {
  font-family: "Titillium Web", sans-serif;
  font-size: 18px;
  color: #0a304e !important;
}

.nav-item.dropdown:hover, .nav-item:hover {
  background-color: white;
}

.dropdown-content {
  display: none;
  position: absolute;
  background-color: #f9f9f9;
  min-width: 160px;
  box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
  z-index: 1;
}

.nav-item.dropdown:hover .dropdown-content {
  display: block !important; 
}

.dropdown-content .dropdown-item, .dropdown-content .dropdown-divider {
  float: none;
  color: black;
  display: block;
  text-align: left;
}
.dropdown-content .dropdown-item:hover {
  background-color: whitesmoke;
  color: cornflowerblue;  
}
.dropdown-content .dropdown-item {
  cursor: pointer;
}
.navbar-toggler {
  font-family: "Titillium Web", sans-serif;
  font-size: 18px;
  color: #0a304e !important;
}

.navbar.navbar-default {
  padding-left: -15px;
  padding-right: -15px;
}
.background-nav {
    background-color: #0a304e;
    border-bottom: 1px solid #fff;

}
.dept-container {
    background-color: #0a304e;
    height: 105px;
}
.custom-margin {
    margin-bottom: 0;
}

.dept-head {
    color: #fff;
    font-weight: 100;
    font-size: 40px; 
    font-family: "Titillium Web",sans-serif;
    line-height: 1.1;
}

.search-div {
    position: relative;
}

.input-search {
    border-radius: 30px;
    color: #626262;
    font-size: 14px;
    font-style: italic;
    font-weight: bold;
}

.magnifying-glass {
    position: absolute;
    left: 85%;
    top: 20%;
    z-index: 999;
    cursor: pointer;
}
.carousel-background {
    background-color:#0A304E;
    font-family: "Titillium Web",sans-serif;
    right: 0;
    left: 0;
    bottom: 0;
    text-align: left;
    padding: 10px 20px 15px;
    z-index: 0;
    height: 75px;
}

.carousel-control-prev-icon {
    font-size: 100px;
}

.custom-size {
    height: 320px;
}

.carousel-caption p {
    margin-bottom: 0;
}
.breadcrumb {
  text-transform: uppercase;
  color: #626262;
  font-size: 12px;
  font-weight: 700;
  margin-top: 20px;
  border-bottom: 1px solid #eee;
  background: 0 0;
  margin: 0px;
  border-radius: 0;
  line-height: 1.42857;
}

.breadcrumb a {
  color: #0a304e !important;
}

.breadcrumb li::before {
  content: ">";
  padding-left: 5px;
  padding-right: 3px;
  font-size: 14px;
  color: #0a304e !important;
}
.wrapper {
  min-height: 100%;
  padding-bottom: -215px;
  background-color: #fff;
}

footer.footer {
    background-color: #f5f5f5;
    height: 520px;
    font-family: "Titillium Web",sans-serif;
    font-size: 14px;
    max-height: 400px;
}

.footer-logo {
    width: 260px;
    height: auto;
}

footer a.link {
    color:#3168a6;
}

footer a.link:hover {
    color: #0a304e;
    text-decoration: underline;
}

footer h3 {
    font-weight: 600;
    font-size: 18px;
    text-transform: uppercase;
}

footer i {
    position: absolute;
    left: -20px;
}

  
body {
    background-color: #f5f5f5;
}
